import {
  KafkaCallbackResponse,
  XodBaseMessageType,
} from '@xip-online-data/types';
import { StatusCodes } from 'http-status-codes';

/**
 * The kafka library will add message-context to this response.
 * So these responses only require minimal processing information
 */

export function Ok(
  additionalMessage?: string,
): (message: XodBaseMessageType) => Promise<KafkaCallbackResponse> {
  return async (message: XodBaseMessageType) => {
    return {
      success: true,
      statusCode: StatusCodes.OK,
      message: `Ok${additionalMessage ? `: ${additionalMessage}` : ''}`,
      payload: message.payload,
      meta: {
        testRun: message.testRun,
        ...(message.meta ?? {}),
      },
    };
  };
}

